import Fetch from 'isomorphic-unfetch';

class WeatherMap extends React.Component{

  state = {}

  componentDidMount () {
      fetch('https://tile.openweathermap.org/map/temp_new/1/1/1.png?appid=038b0cc9c6214552f03247312f5f166a')
        .then(data => this.setState({data}));
    }


  /*getWeatherData = async() => {
    try{
    const res = await fetch('https://tile.openweathermap.org/map/temp_new/1/1/1.png?appid=038b0cc9c6214552f03247312f5f166a');
    const data = await res;
    console.log(data);

    return data;
    }
    catch(e){
      return e.message;
    }
  }*/

  render(){
    return(
      <div>
        <img src={this.state} alt="Logo" />
      </div>
    );
  }
}

export default WeatherMap;
